@tailwind base;
/* Let's get this party started */
::-webkit-scrollbar {
  @apply w-2;
}
/* Track */
::-webkit-scrollbar-track {
  @apply bg-line-dimmed/0;
}
/* Handle */
::-webkit-scrollbar-thumb {
  @apply bg-line rounded-xl;
}
::-webkit-scrollbar-thumb:hover {
    @apply bg-content-dimmed;
  }
::-webkit-scrollbar-thumb:window-inactive {
  @apply bg-line/50;
}
::-webkit-search-cancel-button {
  @apply hidden;
}
hr {
  @apply border-line-dimmed;
}
label,
.label {
  @apply text-sm font-semibold;
}
label {
  @apply inline-block cursor-pointer;
}
/* Headings */
h1 {
  @apply text-2xl font-bold;
}
h2 {
  @apply text-lg font-bold;
}
h3 {
  @apply text-base font-bold;
}
select option {
  @apply bg-main text-content;
}
fieldset {
  @apply flex flex-col gap-2;
}
x-uuid {
  @apply text-xs text-content-dimmed font-mono;
}
@tailwind components;
.container {
  @apply max-w-4xl;
}
.tippy-content {
  @apply text-xs;
}
.ti {
  @apply text-2xl;
}
.box {
  @apply p-4 border border-line-dimmed rounded-xl;
}
.box[data-density='comfortable'] {
    @apply p-8 sm:px-16 sm:py-12;
  }
.box.box-selected {
    @apply bg-gradient-to-tl from-transparent to-line-super-dimmed;
  }
.loading {
  @apply rounded-lg bg-line-dimmed animate-pulse;
}
.avatar {
  @apply relative w-10 h-10 rounded-full bg-intermediate text-intermediate-content flex items-center justify-center font-bold shrink-0 text-xs uppercase whitespace-nowrap;
}
.avatar > img {
    @apply absolute top-0 left-0 object-cover w-full h-full rounded-full;
  }
.avatar > svg {
    @apply w-6 h-6;
  }
.avatar.avatar-sm {
    @apply w-6 h-6;
  }
.avatar.avatar-sm > svg {
      @apply w-3 h-3;
    }
.avatar.avatar-sm > i {
      @apply text-xs;
    }
.avatar.avatar-xs {
    @apply w-4 h-4;
  }
.avatar.avatar-xs > svg {
      @apply w-2 h-2;
    }
.avatar.avatar-xs > i {
      @apply text-xs;
    }
a.full {
  @apply absolute top-0 left-0 w-full h-full;
}
.gallery li:nth-of-type(15n + 1) {
    @apply col-span-2 row-span-2;
  }
.button {
  @apply inline-flex items-center justify-center gap-2
        
        px-6 h-12 rounded-lg 

        text-button-content 
        bg-button 
        
        text-center 
        text-base
        font-semibold 

        transition-colors
        
        hover:outline
        focus:outline
        outline-1
        outline-offset-2
        outline-button
        whitespace-nowrap;
}
.button:disabled {
    @apply bg-line-dimmed
            text-content;
  }
button .spinner {
    @apply hidden;
  }
button[processing] {
    @apply opacity-50
            cursor-not-allowed 
            pointer-events-none;
  }
button[processing] .spinner {
    @apply block;
  }
button:disabled {
    @apply opacity-50 
              cursor-not-allowed 
              pointer-events-none;
  }
/* Styles */
.button-accent {
  @apply bg-button-accent text-button-accent-content outline-button-accent;
}
.button-dimmed {
  @apply bg-button-dimmed text-button-dimmed-content outline-button-dimmed;
}
.button-failure {
  @apply bg-failure outline-failure;
}
.button-outline {
  @apply bg-transparent 
        border 
        border-line-dimmed 
        text-content
        hover:border-line
        focus:border-line
        hover:outline-none
        focus:outline-none;
}
/* Sizes */
.button-sm,
.button-xs {
  @apply h-9 px-3 text-sm rounded-md;
}
.button-sm i, .button-xs i {
    @apply text-base;
  }
.button-sm .spinner, .button-xs .spinner {
    @apply size-3;
  }
.button-xs {
  @apply h-6 px-1.5;
}
.input {
  @apply bg-transparent w-full rounded-lg border border-line h-12 px-3 py-0 appearance-none font-normal text-base flex items-center placeholder:text-content-super-dimmed;
}
.input:disabled {
    @apply bg-line-dimmed cursor-not-allowed opacity-50;
  }
select.input {
  @apply pr-8 cursor-pointer;
}
/* Textarea */
textarea.input {
  @apply min-h-[48px] resize-y h-auto py-3;
}
.autogrow-textarea {
  @apply grid after:whitespace-pre-wrap after:invisible after:row-start-1 after:col-start-1 after:row-end-2 after:col-end-2;
}
.autogrow-textarea:after {
    content: attr(data-replicated-value) ' ';
  }
.autogrow-textarea > textarea {
    @apply resize-none overflow-hidden row-start-1 col-start-1 row-end-2 col-end-2;
    font: inherit;
  }
/* Radio inputs */
input[type='radio'].radio-button {
  @apply hidden;
}
input[type='radio'].radio-button + span {
    @apply inline-flex px-3 py-1 border rounded-lg cursor-pointer border-line hover:border-content;
  }
input[type='radio'].radio-button:checked + span {
    @apply bg-button border-button text-button-content cursor-default;
  }
input[type='radio'].radio-button:disabled + span {
    @apply border-line-dimmed cursor-not-allowed opacity-75;
  }
/* Styles */
.input-dimmed {
  @apply bg-button-dimmed text-button-dimmed-content;
}
/* Sizes */
.input-sm,
.input-xs {
  @apply h-9 px-3 text-sm rounded-md;
}
.input-xs {
  @apply h-6 px-1.5;
}
/* Label helpers */
label:has(+ [required]),
label:has(+ * [required]) {
  @apply before:text-failure before:content-['*'] before:text-xs;
}
label .help {
  @apply text-base text-content-dimmed transition-all hover:scale-125 hover:text-intermediate-content;
}
.badge {
  @apply px-1.5 h-6 text-xs bg-intermediate text-intermediate-content rounded-md flex items-center gap-1;
}
button.badge,
  a.badge {
    @apply border border-transparent hover:border-line;
  }
.badge i {
    @apply text-sm;
  }
.badge:disabled {
    @apply opacity-50 
            cursor-not-allowed 
            pointer-events-none
            bg-line-dimmed
            text-content;
  }
.badge-success {
  @apply bg-success/10 text-success;
}
.badge-alert {
  @apply bg-alert/10 text-alert;
}
.badge-failure {
  @apply bg-failure/10 text-failure;
}
.badge-info {
  @apply bg-info/10 text-info;
}
.tool-icon {
  @apply flex items-center justify-center relative text-main bg-content rounded-[45%];
}
.tool-icon div {
    @apply absolute w-full h-full rounded-[45%] -z-10 top-0 left-0;
    animation: tooliconanim 5s infinite both alternate ease-in-out;
  }
@keyframes tooliconanim {
  0% {
    opacity: 0.8;
    transform: rotate(0deg) scale(1);
  }

  10% {
    opacity: 0.7;
    transform: rotate(15deg) scale(1);
  }

  20% {
    opacity: 0.65;
    transform: rotate(35deg) scale(1.05);
  }

  30% {
    opacity: 0.55;
    transform: rotate(65deg) scale(1.1);
  }

  40% {
    opacity: 0.65;
    transform: rotate(135deg) scale(1.05);
  }

  50% {
    opacity: 0.8;
    transform: rotate(180deg) scale(1);
  }

  60% {
    opacity: 0.7;
    transform: rotate(200deg) scale(0.95);
  }

  70% {
    opacity: 0.65;
    transform: rotate(235deg) scale(0.9);
  }

  80% {
    opacity: 0.55;
    transform: rotate(275deg) scale(0.95);
  }

  90% {
    opacity: 0.75;
    transform: rotate(325deg) scale(1.05);
  }

  100% {
    opacity: 0.8;
    transform: rotate(360deg) scale(1);
  }
}
@keyframes enter {
  from {
    @apply drop-shadow grayscale opacity-0 bg-transparent;
    transform: scale(1.05) translateX(var(--x, 0px));
  }
}
@keyframes enter-content {
  from {
    transform: translateY(10px);
    opacity: 0.2;
  }
}
.preset-card {
  --view: 0px;

  animation: enter both linear;
  animation-timeline: view(36px);
  animation-range: entry;
}
.preset-card h3,
  .preset-card p,
  .preset-card .avatar {
    animation: enter-content both linear;
    animation-timeline: view(var(--view)));
    animation-range: entry;
  }
.preset-card:nth-child(3n + 1 of .preset-card) {
    --x: -36px;
    transform-origin: left center;
  }
.preset-card:nth-child(3n + 1 of .preset-card) .avatar {
      --view: 200px;
    }
.preset-card:nth-child(3n + 1 of .preset-card) h3,
    .preset-card:nth-child(3n + 1 of .preset-card) p {
      --view: 3000px;
    }
.preset-card:nth-child(3n + 2 of .preset-card) {
    --x: 0%;
  }
.preset-card:nth-child(3n + 2 of .preset-card) .avatar {
      --view: 50px;
    }
.preset-card:nth-child(3n + 2 of .preset-card) h3,
    .preset-card:nth-child(3n + 2 of .preset-card) p {
      --view: 100px;
    }
.preset-card:nth-child(3n + 3 of .preset-card) {
    --x: 36px;
    transform-origin: right center;

  }
.preset-card:nth-child(3n + 3 of .preset-card) .avatar {
      --view: 200px;
    }
.preset-card:nth-child(3n + 3 of .preset-card) h3,
    .preset-card:nth-child(3n + 3 of .preset-card) p {
      --view: 3000px;
    }
.hljs {
  color: #383a42;
  background: #fafafa;
}
.hljs::-moz-selection, .hljs ::-moz-selection {
  background-color: #e5e5e6;
  color: #383a42;
}
.hljs::selection,
.hljs ::selection {
  background-color: #e5e5e6;
  color: #383a42;
}
/* purposely do not highlight these things */
.hljs-formula,
.hljs-params,
.hljs-property {
}
/* base03 - #a0a1a7 -  Comments, Invisibles, Line Highlighting */
.hljs-comment {
  color: #a0a1a7;
}
/* base04 - #696c77 -  Dark Foreground (Used for status bars) */
.hljs-tag {
  color: #696c77;
}
/* base05 - #383a42 -  Default Foreground, Caret, Delimiters, Operators */
.hljs-subst,
.hljs-punctuation,
.hljs-operator {
  color: #383a42;
}
.hljs-operator {
  opacity: 0.7;
}
/* base08 - Variables, XML Tags, Markup Link Text, Markup Lists, Diff Deleted */
.hljs-bullet,
.hljs-variable,
.hljs-template-variable,
.hljs-selector-tag,
.hljs-name,
.hljs-deletion {
  color: #ca1243;
}
/* base09 - Integers, Boolean, Constants, XML Attributes, Markup Link Url */
.hljs-symbol,
.hljs-number,
.hljs-link,
.hljs-attr,
.hljs-variable.constant_,
.hljs-literal {
  color: #d75f00;
}
/* base0A - Classes, Markup Bold, Search Text Background */
.hljs-title,
.hljs-class .hljs-title,
.hljs-title.class_ {
  color: #c18401;
}
.hljs-strong {
  font-weight: bold;
  color: #c18401;
}
/* base0B - Strings, Inherited Class, Markup Code, Diff Inserted */
.hljs-code,
.hljs-addition,
.hljs-title.class_.inherited__,
.hljs-string {
  color: #50a14f;
}
/* base0C - Support, Regular Expressions, Escape Characters, Markup Quotes */
.hljs-built_in,
.hljs-doctag, /* guessing */
.hljs-quote,
.hljs-keyword.hljs-atrule,
.hljs-regexp {
  color: #0184bc;
}
/* base0D - Functions, Methods, Attribute IDs, Headings */
.hljs-function .hljs-title,
.hljs-attribute,
.ruby .hljs-property,
.hljs-title.function_,
.hljs-section {
  color: #4078f2;
}
/* base0E - Keywords, Storage, Selector, Markup Italic, Diff Changed */
.hljs-type,
.hljs-template-tag,
.diff .hljs-meta,
.hljs-keyword {
  color: #a626a4;
}
.hljs-emphasis {
  color: #a626a4;
  font-style: italic;
}
.hljs-meta,
/*
  prevent top level .keyword and .string scopes
  from leaking into meta by accident
*/
.hljs-meta .hljs-keyword,
.hljs-meta .hljs-string {
  color: #986801;
}
.hljs-meta .hljs-keyword,
/* for v10 compatible themes */
.hljs-meta-keyword {
  font-weight: bold;
}
