.editor {
  @apply prose 
    max-w-none 
    font-editor 
    prose-headings:font-editor-heading
    prose-headings:font-semibold
    prose-h1:text-2xl
    prose-h2:text-xl
    prose-h3:text-lg
    prose-h4:text-base
    prose-pre:p-6
    prose-pre:relative
    prose-pre:rounded-lg
    prose-pre:bg-line-super-dimmed;
}
.editor > *:first-child {
    @apply mt-0;
  }
.editor > *:last-child {
    @apply mb-0;
  }
.editor pre {
    @apply whitespace-pre-wrap border border-line-dimmed has-[.actions]:pt-0;
  }
.editor pre svg {
    @apply absolute top-0 left-0 w-full h-full rounded-lg text-line-dimmed group-hover:text-line group-data-[selected]:hidden -z-10;
  }
.editor pre .actions {
    @apply flex items-center gap-2 text-content-dimmed justify-between px-6 py-1 mb-3 bg-line-dimmed -mx-6;
  }
.editor pre .lang {
    @apply uppercase pointer-events-none text-xs;
  }
.editor pre .copy {
    @apply cursor-pointer hover:text-content;
  }
/* Dropdown context menu */
.menu {
  @apply absolute 
    z-20 
    
    right-0 
    mt-1 
    top-full
    
    hidden
    border 
    rounded-lg 
    
    border-line-dimmed 
    bg-main 
    text-content
    
    data-[open]:block  
    
    whitespace-nowrap
    
    max-h-max
    overflow-auto
    text-sm
    min-w-32;

  box-shadow: 0px 4px 6px -2px rgba(0, 0, 9, 0.03),
    0px 12px 16px -4px rgba(0, 0, 9, 0.08);
}
.menu.menu-bl {
    @apply left-0 right-auto;
  }
.menu.menu-b {
    @apply left-1/2 right-auto -translate-x-1/2;
  }
.menu.menu-tr {
    @apply right-0 left-auto bottom-full top-auto mt-0 mb-1;
  }
.menu.menu-tl {
    @apply left-0 right-auto bottom-full top-auto mt-0 mb-1;
  }
modal-element {
  @apply fixed top-0 left-0 h-full w-full z-50 bg-content/10 transition-all opacity-0 invisible overflow-y-auto;
}
modal-element.open {
    @apply visible opacity-100 backdrop-blur-lg;
  }
.modal {
  @apply p-8 pt-11 rounded-t-[2rem] md:rounded-xl bg-main shadow-2xl
    fixed bottom-0 left-0 w-full max-h-[85%] overflow-auto

    before:absolute before:top-4 before:left-1/2 before:-translate-x-1/2 before:w-[40px] before:h-1 before:bg-line before:rounded-full before:transform before:transition-all before:group-[[clicked]]/modal:scale-x-125
    
    md:max-h-none
    md:overflow-visible
    md:before:hidden
    md:pt-8
    md:w-[480px]
    md:absolute 
    md:-translate-x-1/2 
    md:-translate-y-1/2 
    md:bottom-auto
    md:top-1/2 
    md:left-1/2 
    
    transition-all 
    md:group-[[clicked]]/modal:scale-105;
}
@keyframes msg {
  from {
    @apply opacity-0;
    transform: translateY(var(--y, 1));
  }
}
@keyframes shine {
  from {
    -webkit-mask-position: 150%;
  }
  to {
    -webkit-mask-position: -50%;
  }
}
.message .tool-processing {
    @apply text-content flex;
    -webkit-mask-image: linear-gradient(
      -75deg,
      rgba(var(--color-content) / 0.4) 30%,
      rgb(var(--color-content)) 50%,
      rgba(var(--color-content) / 0.4) 70%
    );
    -webkit-mask-size: 200%;
    animation: shine 2s linear infinite;
  }
